var Dom = require('@ali/kissy-dom-base');

Dom.getEmptyIframeSrc = function (win) {
    win = win || window;
    win = Dom.get(win);
    if (Dom.isCustomDomain(win)) {
        /*jshint scripturl: true*/
        return 'javascript:void(function(){' + encodeURIComponent(
            'document.open();' +
                'document.domain="' +
                win.document.domain + '";' +
                'document.close();') + '}())';
    }
    return '';
};

Dom.nodeName = function (selector) {
    var el = Dom.get(selector);
    var nodeName = el.nodeName.toLowerCase();

    // http://msdn.microsoft.com/en-us/library/ms534388(VS.85).aspx
    var scopeName = el.scopeName;
    if (scopeName && scopeName !== 'HTML') {
        nodeName = scopeName.toLowerCase() + ':' + nodeName;
    }
    return nodeName;
};


