/**
 * @ignore
 * form data  serialization util
 * @author yiminghe@gmail.com
 */
var util = require('@ali/kissy-util-base');
var Dom = require('@ali/kissy-dom-base');
var querystring = require('@ali/kissy-querystring');

var rselectTextarea = /^(?:select|textarea)/i,
    rCRLF = /\r?\n/g,
    FormSerializer,
    rinput = /^(?:color|date|datetime|email|hidden|month|number|password|range|search|tel|text|time|url|week)$/i;

function normalizeCRLF(v) {
    return v.replace(rCRLF, '\r\n');
}

FormSerializer = {
    /**
     * form serialization
     * @method
     * @param {HTMLElement[]|HTMLElement|Node} forms form elements
     * @return {String} serialized string represent form elements
     * @param {Boolean}[serializeArray=false] See {@link KISSY#method-param} 
     * @member IO
     * @static
     */
    serialize: function(forms, serializeArray) {
        // , []
        return querystring.stringify(FormSerializer.getFormData(forms), undefined, undefined,
            serializeArray || false);
    },

    getFormData: function(forms) {
        var elements = [], data = {};
        util.each(Dom.query(forms), function(el) {
            // form 
            // 
            var subs = el.elements ? elementsToArray(el.elements) : [el];
            elements.push.apply(elements, subs);
        });
        // 
        elements = util.filter(elements, function(el) {
            // 
            return el.name &&
                    // 
                !el.disabled &&
                (
                    // radio,checkbox 
                    el.checked ||
                        // select  textarea
                    rselectTextarea.test(el.nodeName) ||
                        // input 
                    rinput.test(el.type)
                );

            // 
        });
        util.each(elements, function(el) {
            var val = Dom.val(el), vs;

            // <select></select> select nothing!
            // #297
            if (val === null) {
                return;
            }

            // 
            if (util.isArray(val)) {
                val = util.map(val, normalizeCRLF);
            } else {
                val = normalizeCRLF(val);
            }

            vs = data[el.name];
            if (vs === undefined) {
                data[el.name] = val;
                return;
            }
            if (!util.isArray(vs)) {
                // 
                vs = data[el.name] = [vs];
            }
            vs.push.apply(vs, util.makeArray(val));
        });
        return data;
    }
};

// do not pass form.elements to S.makeArray ie678 bug
function elementsToArray(elements) {
    var ret = [];
    for (var i = 0; i < elements.length; i++) {
        ret.push(elements[i]);
    }
    return ret;
}

module.exports = FormSerializer;



