/**
 * @ignore
 * script transport for kissy io,
 * modified version of S.getScript,
 * add abort ability
 * @author yiminghe@gmail.com
 */
var util = require('@ali/kissy-util-base');
var IO = require('./base');
var OK_CODE = 200,
    ERROR_CODE = 500;

IO.setupConfig({
    accepts: {
        script: 'text/javascript, ' +
            'application/javascript, ' +
            'application/ecmascript, ' +
            'application/x-ecmascript'
    },

    contents: {
        script: /javascript|ecmascript/
    },
    converters: {
        text: {
            //  xhr+eval 
            //  script node 
            //  eval
            script: function(text) {
                util.globalEval(text);
                return text;
            }
        }
    }
});

function ScriptTransport(io) {
    var config = io.config,
        self = this;
    //  xhr+eval , ie 
    if (!config.crossDomain) {
        return new(IO.getTransport('*'))(io);
    }
    self.io = io;
    return self;
}

util.augment(ScriptTransport, {
    send: function() {
        var self = this,
            io = self.io,
            c = io.config;
        self.script = IO.getScript(io._getUrlForSend(), {
            charset: c.scriptCharset,
            success: function() {
                self._callback('success');
            },
            error: function() {
                self._callback('error');
            }
        });
    },

    _callback: function(event, abort) {
        var self = this,
            script = self.script,
            io = self.io;
        // , abort
        if (!script) {
            return;
        }
        self.script = undefined;
        if (abort) {
            return;
        }
        // Callback if not abort
        if (event !== 'error') {
            io._ioReady(OK_CODE, 'success');
        } else if (event === 'error') {
            //  ie<9 
            io._ioReady(ERROR_CODE, 'script error');
        }
    },

    abort: function() {
        this._callback(0, 1);
    }
});

IO.setupTransport('script', ScriptTransport);



