/**
 * this code can only run at browser environment
 * @ignore
 * @author lifesinger@gmail.com, yiminghe@gmail.com
 */

var util = require('./base');
var readyRE = /complete|loaded|interactive/;
var win = typeof window !== 'undefined' ? window : {},
    RE_NOT_WHITESPACE = /\S/;

util.mix(util, {
    /**
     * A crude way of determining if an object is a window
     * @member util
     */
    isWindow: function (obj) {
        // must use == for ie8
        /*jshint eqeqeq:false*/
        return obj != null && obj == obj.window;
    },

    /**
     * Specify a function to execute when the Dom is fully loaded.
     * from zepto http://gitlab.alibaba-inc.com/tb/kimi/blob/master/src/zepto.js
     * @param fn {Function} A function to execute after the Dom is ready
     * @chainable
     * @member util
     */
    ready: function (fn) {
      // need to check if document.body exists for IE as that browser reports
      // document ready when it hasn't yet created the body element
        if (readyRE.test(document.readyState) && document.body) { 
            fn(); 
        } else {
            document.addEventListener('DOMContentLoaded', function() { fn(); }, false);
        }
    },

    /**
     * Evaluates a script in a global context.
     * @member util
     */
    globalEval: function (data) {
        if (data && RE_NOT_WHITESPACE.test(data)) {
            // http://weblogs.java.net/blog/driscoll/archive/2009/09/08/eval-javascript-global-context
            // http://msdn.microsoft.com/en-us/library/ie/ms536420(v=vs.85).aspx always return null
            /*jshint evil:true*/
            if (win.execScript) {
                win.execScript(data);
            } else {
                (function (data) {
                    win['eval'].call(win, data);
                })(data);
            }
        }
    }
    
});


