var util = require('@ali/kissy-util-base');

/*jshint eqeqeq:false*/
// cannot use === for window in ie8
var Dom = require('./api');

var win = window;
var EXPANDO = '_ks_data_' + util.now(); //  kissy  expando 
var dataCache = {}; //  node  data
var winDataCache = {}; // 
// The following elements throw uncatchable exceptions if you
// attempt to add expando properties to them.
var noData = {
  applet: 1,
  object: 1,
  embed: 1
};

var objectOps = {

  data: function(ob, name, value) {
    if (ob == win) {
      return objectOps.data(winDataCache, name, value);
    }
    var cache = ob[EXPANDO];
    if (value !== undefined) {
      cache = ob[EXPANDO] = ob[EXPANDO] || {};
      cache[name] = value;
    } else {
      if (name !== undefined) {
        return cache && cache[name];
      } else {
        cache = ob[EXPANDO] = ob[EXPANDO] || {};
        return cache;
      }
    }
  },

  removeData: function(ob, name) {
    if (ob == win) {
      return objectOps.removeData(winDataCache, name);
    }
    var cache = ob[EXPANDO];
    if (name !== undefined) {
      delete cache[name];
      if (util.isEmptyObject(cache)) {
        objectOps.removeData(ob);
      }
    } else {
      try {
        // ob maybe window in iframe
        // ie will throw error
        delete ob[EXPANDO];
      } catch (e) {
        ob[EXPANDO] = undefined;
      }
    }
  }
};

var domOps = {

  data: function(elem, name, value) {
    if (noData[elem.nodeName.toLowerCase()]) {
      return undefined;
    }
    var key = elem[EXPANDO];
    var cache;
    if (!key) {
      // 
      if (name !== undefined &&
        value === undefined) {
        return undefined;
      }
      // 
      key = elem[EXPANDO] = util.guid();
    }
    cache = dataCache[key];
    if (value !== undefined) {
      // 
      cache = dataCache[key] = dataCache[key] || {};
      cache[name] = value;
    } else {
      if (name !== undefined) {
        return cache && cache[name];
      } else {
        // 
        cache = dataCache[key] = dataCache[key] || {};
        return cache;
      }
    }
  },

  removeData: function(elem, name) {
    var key = elem[EXPANDO];
    if (!key) {
      return;
    }
    var cache = dataCache[key];
    if (name !== undefined) {
      delete cache[name];
      if (util.isEmptyObject(cache)) {
        domOps.removeData(elem);
      }
    } else {
      delete dataCache[key];
      try {
        delete elem[EXPANDO];
      } catch (e) {
        elem[EXPANDO] = undefined;
      }
      if (elem.removeAttribute) {
        elem.removeAttribute(EXPANDO);
      }
    }
  }
};

util.mix(Dom,
  /**
   * @override KISSY.DOM
   * @class
   * @singleton
   */
  {

    __EXPANDO: EXPANDO,

    _dataCache: dataCache,

    _winDataCache: winDataCache,

    /**
     * If name set and data unset Store arbitrary data associated with the specified element. Returns undefined.
     * or
     * If name set and data unset returns value at named data store for the element
     * or
     * If name unset and data unset returns the full data store for the element.
     * @param {HTMLElement[]|String|HTMLElement} selector Matched elements
     * @param {String} [name] A string naming the piece of data to set.
     * @param {*} [data] The new data value.
     * @return {Object|undefined}
     */
    data: function(selector, name, data) {
      var elems = Dom.query(selector);
      var elem = elems[0];

      // supports hash
      if (typeof name === 'object') {
        for (var k in name) {
          Dom.data(elems, k, name[k]);
        }
        return undefined;
      }

      // getter
      if (data === undefined) {
        if (elem) {
          if (elem.nodeType) {
            return domOps.data(elem, name);
          } else {
            // window
            return objectOps.data(elem, name);
          }
        }
      } else {
        for (var i = elems.length - 1; i >= 0; i--) {
          elem = elems[i];
          if (elem.nodeType) {
            domOps.data(elem, name, data);
          } else {
            // window
            objectOps.data(elem, name, data);
          }
        }
      }
      return undefined;
    },

    /**
     * Remove a previously-stored piece of data from matched elements.
     * or
     * Remove all data from matched elements if name unset.
     * @param {HTMLElement[]|String|HTMLElement} selector Matched elements
     * @param {String} [name] A string naming the piece of data to delete.
     */
    removeData: function(selector, name) {
      var els = Dom.query(selector);
      var elem;
      var i;
      for (i = els.length - 1; i >= 0; i--) {
        elem = els[i];
        if (elem.nodeType) {
          domOps.removeData(elem, name);
        } else {
          // window
          objectOps.removeData(elem, name);
        }
      }
    },

    /**
     * clean data from element
     * @param {HTMLElement[]|String|HTMLElement} selector Matched elements
     * @param {Boolean} deep whether clean descendant nodes
     * @param {Object} [DOMEvent] Pass the Kissy's DOM-Event Object to remove all event listeners attached on elements
     */
    cleanData: function(selector, deep, DOMEvent) {
      var els = Dom.query(selector);
      var elem;
      var i;
      for (i = els.length - 1; i >= 0; i--) {
        elem = els[i];
        if (elem.nodeType) {
          var descendants = deep && util.makeArray(elem.getElementsByTagName('*')) || [];
          descendants.push(elem);
          for (var j = 0, len = descendants.length; j < len; j++) {
            domOps.removeData(descendants[j]);
          }
          if (DOMEvent) {
            DOMEvent.detach(descendants);
          }
        } else {
          // window
          objectOps.removeData(elem);
        }
      }
    }
  });

